body{
  background-color: #00127F;
}

.App{
  background-color:#4D67FF;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-list input{
  background-color: #2A2C88;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
#to-do-list input::placeholder{
  color:rgba(red, green, blue, 0,5)
}
#to-do-list input:focus,#to-do-list button:focus{
  outline: 0;
}
#to-do-list button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #C8A904;
  cursor: pointer;
  font-weight: bolder;
  color: white;

}
#to-do-list input{
  background-color: #2A2C88;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
